
.myInput{
    display: block;
    position: relative;
    flex: 1;
    width: 100%;
}
.myInputBody {
    width: 100%;
    height: 52px;
    padding: 0 12px;
    border: 1px solid var(--light-grey);
    border-radius: 2px;
    cursor: text;
    color: var(--main-text);
}
.myInputBody::placeholder{
    color: var(--grey);
}
.myInputBody:focus{
    border: 1px solid var(--purple);
    outline: none;
}
.myInput:disabled .myInputBody{
    display: block;
    border: 1px solid var(--light-grey);
    outline: none;
    cursor: default;
}
.myInputBody::after{
    content: '';
}
.inputLabel{
    position: absolute;
    top: -32px;
    font-family: 'Factor A';
    font-size: 16px;
    line-height: 20px;
    font-style: normal;
    font-weight: 500;
    color: var(--main-text);
    letter-spacing: -0.0124em;
}
.myInput_error .myInputBody,
.myInput_error .myInputBody:focus{
    border-color: var(--red);
}
@media (max-width: 576px) {
    .inputLabel{
        font-size: 16px;
        line-height: 20px;
    }
    .myInputBody{
        height: 42px;
    }
}